export default {
	data() {
		return {
			tabsState:'',//选项卡状态
			tabsMaskState:'',//选项卡遮罩显示隐藏
			swiperIndex:'',
			pagesize: 0,
			index:7,
			partnerList:[]
		};
	},
// 	},
	onLoad() {
		this.getPartnerList();
	},
	onReachBottom() {
		this.getPartnerList('onReach');
	},
	methods: {	
		// 获取合伙人列表
		getPartnerList(page = '0') {
			var _this = this;
			var oldPartnerList = _this.partnerList;
			if(page === 'onReach'){
				uni.request({
					url:shoppublic.getUrl() + 'partnershipShop/findPartnershipShopList',
					data:{
						index:_this.index,
						city:610100,
						county:'',
						partner:'',
						type:'',
					},
					success: (res) => {
						console.log(res);
						if(page === 'onReach' && res.data.responseBody){
							oldPartnerList = oldPartnerList.concat(res.data.responseBody);
							_this.index += 7;
						}else{
							return false;
							// oldPartnerList = res.data.responseBody;
						}
						_this.partnerList = oldPartnerList;
					}
				})
			}else{
				uni.request({
					url:shoppublic.getUrl() + 'partnershipShop/findPartnershipShopList',
					data:{
						index:_this.pagesize,
						city:610100,
						county:'',
						partner:'',
						type:'',
					},
					success: (res) => {
						console.log(res);
						_this.partnerList = res.data.responseBody;
					}
				})
			}
		},
		// 区域/费用选择器按钮点击事件
		clickScrollCell(e){
			this.closeTabsMask();
		},
		// 顶部选项卡点击事件
		tabsChanges(state){
			this.tabsState = state;
			this.swiperIndex = state;
			this.tabsMaskState = 'true';
		},
		// 选项卡滑块切换
		swiperChangs(e){
			this.tabsState = e.detail.current;
			this.swiperIndex = e.detail.current;
		},
		// 关闭遮罩
		closeTabsMask(){
			this.tabsState = '';
			this.tabsMaskState = '';
			this.swiperIndex = '';
		},
		// 页面跳转至合伙开店发布详情页面
		toPartnershipStoreRelease() {
			uni.navigateTo({
				url: './partnershipStoreRelease/partnershipStoreRelease'
			});
		},
		// 页面跳转至合伙开店详情页面
		toPartnerOpenStoreDateils(id){
			uni.navigateTo({
				url: './partnerOpenStoreDateils/partnerOpenStoreDateils?key=' + id
			});
		}
	}
}
